import React from "react"
import { graphql, Link, useStaticQuery } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const Projects = () => {
  const allProjectsJson = useStaticQuery(
    graphql`
      query names{
        allProjectsJson {
          edges {
            node {
              name,
              description
            }
          }
        }
      }
    `
  );

  return (
    <Layout>
      <SEO title="projects" />
      <h1>projects</h1>
      <p>projects</p>
      <ul>
        {allProjectsJson.edges.map((edge,i) => <li key={i}>{JSON.stringify(edge)}</li>)}
      </ul>
    </Layout>
  );
}

export default Projects
